.myProcess{
    max-width: 1200px;
    width: 90%;
    margin: auto;
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
.title{
    font-size: 2.5rem;
    font-weight: 700;
    color: #ffc745;
    text-align: center;
}
.description{
    font-size: 1.2rem;
    font-weight: 400;
    color: #ffc745;
    text-align: center;
    max-width: 800px;
}
.stepsContainer{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    width: 100%;
}
.stepsContainer>div{
    background: transparent;
    padding: 20px;
    border: 2px solid #ffc745;
    border-radius: 20px;
    height: -webkit-fill-available;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.stepsContainer>div:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
}
.stepHeader{
    font-size: 50px;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-bottom: 15px;
    border-radius: 50%;
    background: white;
    color: #002424;
    font-weight: 700;
}
.stepTitle{
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffc745;
    margin-bottom: 10px;
}
.stepDescription{
    font-size: 1rem;
    font-weight: 400;
    color: #fff;
    line-height: 1.5;
}
@media (max-width: 900px) {
    .stepsContainer{
        grid-template-columns: 1fr 1fr;
    }
}
@media (max-width: 600px) {
    .stepsContainer{
        grid-template-columns: 1fr;
    }
    .stepHeader{
        font-size: 40px;
        width: 50px;
        height: 50px;
    }
}