.chatBoxContainer {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
  font-family: Arial, sans-serif;
}

/* Floating button */
.chatButton {
  background: #0078ff;
  color: #fff;
  border: none;
  padding: 12px 18px;
  border-radius: 30px;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: background 0.3s;
}

.chatButton:hover {
  background: #005fcc;
}

/* Chat box */
.chatBox {
  width: 320px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: height 0.3s ease;
}

/* Header */
.title {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  color: #0078ff;
  padding: 12px;
  border-bottom: 1px solid #ddd;
  display: inline-block;
}

.closeButton {
  float: right;
  cursor: pointer;
  color: #888;
  font-size: 18px;
  margin: 12px;
  transition: color 0.2s;
}

.closeButton:hover {
  color: #000;
}

/* Messages */
.messages {
  flex: 1;
  padding: 12px;
  overflow-y: auto;
}

.userContainer {
  display: flex;
  align-items: flex-end;
  flex-direction: row-reverse;
  margin-bottom: 10px;
  align-items: end;
}
.botContainer {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  margin-bottom: 10px;
  align-items: end;
}

.user {
  margin-right: 8px;
  font-size: 18px;
  color: #0078ff;
}

.userMessage {
  background: #0078ff;
  color: white;
  padding: 10px 14px;
  border-radius: 12px 12px 0 12px;
  max-width: 70%;
  word-wrap: break-word;
}

.botMessage {
  background: #f1f1f1;
  color: #333;
  padding: 10px 14px;
  border-radius: 12px 12px 12px 0;
  max-width: 70%;
  word-wrap: break-word;
}

.text {
  font-size: 14px;
  line-height: 1.4;
}

/* Input area */
.inputContainer {
  display: flex;
  border-top: 1px solid #ddd;
}
.input {
  border: none;
  border-top: 1px solid #ddd;
  padding: 12px;
  font-size: 14px;
  outline: none;
  width: calc(100% - 80px);
    resize: none;
    height: 50px;
}

.button {
  border: none;
  background: #0078ff;
  color: white;
  padding: 12px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.3s;
  width: 80px;
}

.button:hover {
  background: #005fcc;
}
